@import "variables";

body{
  .header{
    .container{
      .header__nav-panel{
        &__goods{
          .dropMenu {
            width: 60%;
            position: absolute;
            z-index: 10;
            padding: 0 25px 25px 0;
            display: flex;
            flex-wrap: wrap;
            background-color: @hoverColor;
            li {
              margin: 20px 0 0 25px;
              width: 30%;
              display: flex;
              flex-direction: column;
              box-sizing: border-box;
              a {
                display: flex;
                justify-content: flex-start;
                color: #fff;
                text-align: left;
                border: none;
                background-color: @hoverColor;
                &:hover {
                  text-decoration: underline;
                  background: @hoverColor;
                }
              }
              &:nth-child(1) {
                font: 1em/1.125em @sansbold;
                i {
                  line-height: 1.125em;
                }
              }
              ul {
                display: flex;
                flex-direction: column;
                li {
                  margin: 0 0 0 25px;
                  width: 100%;
                  a {
                    font: 1em/1.125em @sansregular;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__nav-panel{
          &__goods{
            .dropMenu {
              width: 60%;
              top: 0;
              left: 300px;
              li{
                position: relative;
                margin: 5px 0 0 25px;
                width: 100%;
                ul{
                  display: none;
                }
                &:hover{
                  ul{
                    li{
                      display: none;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}